<script>
import { reactive, toRefs, getCurrentInstance } from "vue";

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      dialogVisible: false,
      dataobj: {
        matters: "",
        hearer: "",
        date: "",
      },
    });
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    return {
      setshow,
      setedit,
      ...toRefs(state),
    };
  },
};
</script>

