<template>
    <!-- 点击编辑进入客户管理编辑界面 -->
    <div class="cus_box">
        <el-tabs class="eltabs" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane class="eltabpane" label="客户信息" name="cus_info">
                <cusinfo ref="cusinfo"></cusinfo>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="跟进记录" name="follow_record">
                <followrecord ref="followrecord"></followrecord>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="合同信息" name="contract_info">
                <contractinfo ref="contractinfo"></contractinfo>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="证照资质" name="license_qua">
                <license ref="license"></license>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="客户关系" name="cus_relation"><cusRelation ref="cusRelation"></cusRelation></el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="重大事件" name="major_events"><majorEvents ref="majorEvents"></majorEvents></el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="操作日志" name="optionlog">
                <optionlog ref="optionlog"></optionlog>
            </el-tab-pane>
        </el-tabs>
        <div class="return_btn">
            <el-button v-if="activeName=='cus_info'" size="mini" type="primary">保存</el-button>
            <el-button v-if="isouter==1" @click="returnrouter" size="mini">返回</el-button>
        </div>
    </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, toRefs } from 'vue'
import cusinfo from './cus_info.vue'
import followrecord from './follow_record.vue'
import contractinfo from './Contractinfo.vue'
import majorEvents from './major_events.vue'
import cusRelation from './cus_relation.vue'
import optionlog from './Operation_log.vue'
import license from './License_qualification.vue'
export default {
    components:{cusinfo,followrecord,contractinfo,majorEvents,cusRelation,optionlog,license},
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            activeName:'cus_info',
            isedit:null,
            isouter:null
        })
        const handleClick = (tab,event) =>{
            console.log(tab)
            console.log(event)
        }
        const returnrouter = () =>{
            // console.log(proxy.$route)
            proxy.$router.go(-1)
        }
        onMounted(()=>{
            proxy.isedit = proxy.$route.query.isedit
            proxy.isouter = proxy.$route.query.isouter
            
            proxy.$refs.cusinfo.setstatus(proxy.$route.query.isedit)
        })
        return {
            ...toRefs(state),
            handleClick,
            returnrouter
        }
    }
}
</script>

<style scoped>
    .cus_box{
        background: #fff;
        height: 100%;
        position: relative;
    }
    .return_btn{
        position: absolute;
        right: 10px;
        top: 5px;
    }
</style>
<style>
    .eltabs.el-tabs .el-tabs__nav-scroll{
        padding: 0 10px;
        box-sizing: border-box;
    }
    .eltabs.el-tabs .el-tabs__content{
        padding: 0 10px;
        padding-top: 10px;
        box-sizing: border-box;
        background: #fff;
    }
</style>