<template>
  <div>
    <!-- 客户管理的证照资质 -->
    <div class="follow_box">
      <div class="query_btn">
        <el-button @click="newadd" size="mini" type="primary">添加</el-button>
      </div>
      <div class="table_box">
        <el-table
          :height="$store.state.innerheight - 210"
          size="mini"
          :data="tableData"
          border
          style="width: 100%"
          :header-cell-style="{ background: '#f5f5f5' }"
        >
          <el-table-column prop="licencetype" label="证照类型" />
          <el-table-column prop="startdate" label="开始日期" />
          <el-table-column prop="enddate" label="结束日期" />
          <el-table-column label="操作" width="200">
            <template #default="scope">
              <el-button size="mini" type="text" @click="editdata(scope.row)"
                >编辑</el-button
              >
              <el-button size="mini" type="text" style="color: red"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="footer_box">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
      <license ref="license"></license>

    </div>
  </div>
</template>

<script>
import { reactive, toRefs,getCurrentInstance } from "vue";
import license from './license_detail.vue'
export default {
    components:{
        license
    },
  setup() {
    const { proxy } = getCurrentInstance();

    const state = reactive({
      tableData: [
          {
               licencetype:"食品营业执照",
               startdate:"2021-09-09",
               enddate:"2021-10-10"
          },
           {
               licencetype:"食品营业执照",
               startdate:"2021-09-09",
               enddate:"2021-10-10"
          },
      
      ],
    });
     const newadd = () => {
      proxy.$refs.license.setshow()
    };
    const editdata=(res)=>{
        console.log(res)
         proxy.$refs.license.setedit(res)
    }

    return {
      ...toRefs(state),
      editdata,
      newadd
    };
  },
};
</script>

<style  scoped>
.query_btn{
    float: right;
    margin:10px 0;
    padding-right: 10px;
    box-sizing: border-box;
}

.table_box{
    padding:10px;
    box-sizing: border-box;
}
</style>